<script>
// import { api, get } from "@api";
// import { mapState, mapMutations } from 'vuex'
// import { formatDate, isNull } from '@/utils'
// eslint-disable-next-line import/no-extraneous-dependencies
import { findLendingInfoByIdList,updateAmountAndStatus, } from '@/service/loan.js'
import HomeSearch from '@views/Home/Home-search.vue'

export default {
  name: 'LoadOne',
  components: {
    'home-search': HomeSearch,
  },
  data() {
    return {
        PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      active: 0,
      tab2: 2,
      loadOneList:[]
    }
  },
  computed: {

  },
  created() {
    this.selectInput = '777'
    this.getfindLendingInfoByIdList()
  },
  methods: {    
    routerTo(e){
      console.log('e',e)
      if(e){
        this.$router.push({
          path:'/addRepayment',
          query:{
            id:this.$route.query.id,
            ln:this.loadOneList.length+1,
            pid:this.$route.query.projectId,
            Tid:e.id
          }
        })
      }else{
        this.$router.push({
          path:'/addRepayment',
          query:{
            id:this.$route.query.id,
            ln:this.loadOneList.length+1,
            pid:this.$route.query.projectId,
          }
        })
      }
      
    },
    changSearch() { },
    //查看当前贷后归还列表
    getfindLendingInfoByIdList(){
      let params = {...this.searchObj}      
      params.projectId = this.$route.query.projectId
      findLendingInfoByIdList(params).then(res=>{
        console.log('查看当前贷后归还列表',res)
        this.loadOneList = res.data.data
      })
    },
    //确认更改状态
    setUpdateAmountAndStatus(item){
      let params = {
        id:item.id,
        projectId:this.$route.query.projectId,
        termRent:item.termRent,
        createUserId:this.$store.getters.userInfo.id
      }
      
      updateAmountAndStatus(params).then(res=>{
        console.log('确认更改状态',res)
        this.getfindLendingInfoByIdList()
        // this.loadOneList = res.data
      })
    },
    // 控制筛选
    getSearch(data) {
      console.log('动态列表控制筛选',data)
      this.searchObj = data      
      let searchType = false // search是否变化
      this.homeListData = []
        this.page = 1
        this.total = null
        this.loading = true
      this.finished_text = ''
      this.pageLog  = 1
      this.getfindLendingInfoByIdList()
    },
    // 控制筛选    
    clearDataSearch(data) {
      // 清除筛选数据
      this.getSearch()
      
    },
  },
}
</script>
