<script>
// import { api, get } from "@service";
// import { mapState, mapMutations } from 'vuex'
// import { formatDate, isNull } from '@/utils'
// eslint-disable-next-line import/no-extraneous-dependencies
import HomeSearch from '@views/Home/Home-search.vue'
import { rentBackFilePage,updateRentBackFileById,addRentBack } from '@/service/loan.js'
import { upload  } from '@/service/customer'
import { formatDate } from '@/utils'
export default {
  name: 'LoadThree',
  components: {
    'home-search': HomeSearch,
  },
  data() {
    return {
      PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      active: 0,
      popShow: false,
      tab2: 2,
      tabIndex: 1,
      userList: [ ],
      rentBackTypeList:[
        { name:'请选择类型',val:''},
        { name:'财务类型',val:0},
        { name:'基础类型',val:1},
        { name:'征信报告',val:2},
        { name:'其他类',val:3},
      ],
      LoanedList:[],
      totalLog: null,
      loadingLog: false,
      finishedLog: false,
      finished_text: '没有更多数据啦',
      error: false,
      pageLog: 1,
      size: 5,
      newFile:null,
      rentBackType:'',
      search:{
        slt:''
      },
      show:false
    }
  },
  computed: {

  },
  created() {
    this.selectInput = '777'
  },
  methods: {
    changSearch() { },
    //下载
    download(itm){
      console.log('itm',itm)
      let baseUrl = this.$baseUrl()+itm.filePath+'&filename='+itm.fileName
      window.open(baseUrl)
    },
    showPop() {
      this.popShow = true
    },
    
    //'删除文件'
    deleteFile(){
      console.log('this.delItm',this.delItm)
      updateRentBackFileById({id:this.delItm.id}).then(res=>{
        // console.log('删除文件',res)
        this.LoanedList.map((d,idx)=>{
          if(d.id==this.delItm.id){ 
            this.LoanedList.splice(idx,1)
          }          
        })
        this.boxShow()
      })
    },
    getSearch(data) {
      this.searchObj = data
      console.log(this.searchObj)
      this.searchObj = data      
      let searchType = false // search是否变化
      this.LoanedList = []
        this.totalLog = null
        this.loadingLog = true
      this.finished_text = ''
      this.pageLog  = 1
      this.onLoad()
    },
    clearDataSearch(data) {
      // 清除筛选数据
      if (data === 1) {
        // eslint-disable-next-line no-alert
        
        // 企业列表
      } else {
        // 动态列表
      }
    },
    // 加载数据
    onLoad() {
      const that = this
      // console.log(this.LoanedList.length , this.totalLog)
      if (this.LoanedList.length === this.totalLog) {
        this.finishedLog = true // 数据全部加载完成
        this.loadingLog = false// 加载状态结束
        this.finished_text = '没有更多数据'
        return
      }
      // 下拉加载事件
      setTimeout(() => {
        that.getLoanedList()
      }, 1000)      
    },
    //获取当前数据列表
    getLoanedList() {
      let params = {...this.searchObj}
        params.projectId = this.$route.query.projectId,
        params.fileType=1,
        params.page=this.pageLog,
        params.size=this.size
      
      rentBackFilePage(params).then((res) => {        
        this.LoanedList = this.LoanedList.concat(res.data)
        this.LoanedList.map(d=>{
          if(d.rentBackType == 1){
            d.rentBackType = '基础类型'
          }else if(d.rentBackType == 2){
            d.rentBackType = '征信报告'
          }else if(d.rentBackType == 3){
            d.rentBackType = '其他类'
          }else if(d.rentBackType == 0){
            d.rentBackType = '财务类型'
          }
        })
        this.totalLog = res.total
        this.pageLog += 1
        this.loadingLog = false// 加载状态结束
        // this.quoteList = addMark(this.quoteList,3)  
      })
    },
    update(e) {
      let file = e.target.files[0]
      let param = new FormData()
      param.append('file', file)
      let userData = this.$store.getters.userData
      let fileName = userData.companyAbbreviation+'-'+userData.companyBusinessLicense+'-'+ formatDate(new Date(),'yyyyMMdd') 
      
      let config = {
        headers: { 'Content-Type': 'multipart/form-data' }
      }
      this.$toast.loading({
        mask: true,
        message: '上传中...',
        duration:100000
      })
      upload(param, config)
        .then(res => {
          console.log('上传文件', res.data)
          this.newFile=res.data
          let name = file.name
          console.log(name)
          
          this.$toast.clear()
          // console.log('文件相关',param, )
          this.fileTypeName = name.slice(name.lastIndexOf('.'),name.length)
          if(res.data){
            this.userList.push({path:res.data.url,name:fileName+this.fileTypeName})
          }
          
        })
    },
    deleteItem(idx){
      console.log(idx)
      this.userList.splice(idx,1)
    },
    //新增贷后资料
    addSearon(){
      if(!this.newFile) return    
      let params = this.userList
      params.map(d=>{
        d.fileName=d.name
        d.filePath=d.path
        d.projectId=this.$route.query.projectId
        d.fileType=1
        d.lendingId=this.$route.query.id
        d.timeQuarter=this.isQuarter
        d.rentBackType=this.rentBackType
        d.userId=this.$store.getters.userInfo.id
        d.companyId=this.$store.getters.userData.companyId
      })
      let paramss = {
        rentBackFileDTOList:params
      }
      
      addRentBack(paramss).then(res=>{
        console.log('新增季度检查',res)
        this.$notifySty('添加成功') 
        this.addTrue()       
      })
    },
    //增加成功后的衔接操作
    addTrue(){      
      this.pageLog = 1
      this.size = 5
      this.totalLog = null
      this.$set(this,'LoanedList',[])
      this.loadingLog = true// 加载状态启动
      this.finished_text = ''
      this.popShow = false
      this.userList = []
      this.rentBackType = ''
      this.onLoad()
    },
    boxShow(itm){
      this.show = !this.show
      if(!this.show)this.delItm = null
      else this.delItm = itm
      console.log('this.delItm',this.delItm)
    },
  },
}
</script>
