<template>
  <div
    name="loadTwo"
    class="loadTwo"
  >
    <div class="year-tab">
      <van-popup
        v-model="show"
        class="Tit-box"
      >
        <div class="Tit-box-title">
          提示
        </div>
        <div class="Tit-box-cont">
          确认删除？
        </div>
        <div class="Tit-box-buts">
          <van-button
            class="Tit-box-but"
            type="primary"
            square
            @click="deleteFile"
          >
            确认
          </van-button>
          <van-button
            class="Tit-box-but"
            square
            @click="boxShow"
          >
            取消
          </van-button>
        </div>
      </van-popup>
      <van-row>
        <van-col span="6">
          <span
            :class="{'theme-colors':isQuarter==1}"
            @click="changeIdx(1,'get')"
          >第一季度</span>
        </van-col>
        <van-col span="6">
          <span
            :class="{'theme-colors':isQuarter==2}"
            @click="changeIdx(2,'get')"
          >半年度</span>
        </van-col>
        <van-col span="6">
          <span
            :class="{'theme-colors':isQuarter==3}"
            @click="changeIdx(3,'get')"
          >第三季度</span>
        </van-col>
        <van-col span="6">
          <span
            :class="{'theme-colors':isQuarter==4}"
            @click="changeIdx(4,'get')"
          >整年度</span>
        </van-col>
      </van-row>
    </div>
    <div class="contact_container5">
      <van-list
        v-model="loadingLog"
        :finished="finishedLog"
        :finished-text="finished_text"
        :error.sync="error"
        error-text="请求失败，点击重新加载"
        @load="onLoad()"
      >
        <div class="tab5_box">
          <div class="tab5-count">
            <!-- <span>2019年</span> -->
            <div
              class="search-item"
              style="display:flex"
            >
              <div class="left text-bold">
                请选择时间
              </div>
              <div
                class="right"
                style="margin-left: 10px;"
              >
                <select
                  v-model="newYear"  
                  @change="changeYear"                
                >
                  <option
                    v-for="(itm,idx) in yearList"
                    :key="idx"
                    :value="itm"
                  >
                    {{ itm }}
                  </option>
                </select>
              </div>
            </div>
            <!-- <van-icon
              name="arrow"
              style="position:relative;top:2px;"
            /> -->
          </div>
          <div
            v-for="(itm,idx) in LoanedList"
            :key="idx"
            class="tab5-list"
          > 
            <div class="tab5-list-tilte">
              <p class="span1">
                {{ itm.fileName }}
              </p>
              <p
                class="span2"
                style="color:#999999;"
              >
                {{ itm.createTime }}
              </p>
