    >
      <p>该项目模板消息:已发送</p>
      <p>发送时间:{{ sendMessage.date }}</p>
      <p>确认人:{{ sendMessage.sendUsers }}</p>
      <div class="message" />
      <div style="margin-bottom: 15px;margin-bottom: 15px;overflow: hidden;text-overflow: ellipsis;">
        模板消息接收人:{{ sendMessage.sendInfo.toUser }}
      </div>
      <div style="text-align: center;color: #555;">
        <p>{{ sendMessage.sendInfo.textCard.btnTxt }}</p>
        <p>{{ sendMessage.sendInfo.textCard.description }}</p>
        <p>{{ sendMessage.sendInfo.textCard.title }}</p>
      </div>
    </div>

    <div class="kong" />
    <div class="footer">
      <van-row style="display:flex">
        <van-col
          span="8"
          style="flex:1"
