<script>
// import { api, get } from "@api";
// import { mapState, mapMutations } from 'vuex'
import { formatDate, isNull } from '@/utils'
import { rentBackFilePage,updateRentBackFileById,addSearonCheck,sendTextCardMessage,lendingInfoConfirm,getLendingInfoConfirm } from '@/service/loan.js'
import { upload  } from '@/service/customer'
export default {
  name: 'LoadTwo',
  data() {
    return {
      PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      showMoreBox: false,
      LoanedList:[],
      totalLog: null,
      loadingLog: false,
      finishedLog: false,
      finished_text: '',
      error: false,
      pageLog: 1,
      size: 5,
      isQuarter:1,//当前选择季度
      active:1,
      newFile:null,//上传文件寄存
      nowUpType:true,//当前的上传状态
      newYear:'2019',//当前年份
      yearList:['2017','2018','2019','2020'],
      show:false
    }
  },
  computed: {},
  created() {
    this.selectInput = '777'
    console.log(this.isQuarter,this.newYear,this.$route.query.id)
    this.getNowUpType('get')//查询上传状态
  },
  methods: {
    update(e) {            
      let file = e.target.files[0]      
      let param = new FormData()
      param.append('file', file)
      
      let config = {
        headers: { 'Content-Type': 'multipart/form-data' }
      }

      this.$toast.loading({
        mask: true,
        message: '上传中...',
        duration:100000
      })
      upload(param, config)
        .then(res => {
          let name = file.name
          console.log(name)
          // console.log('文件相关',param, )
          this.fileTypeName = name.slice(name.lastIndexOf('.'),name.length)
          console.log('上传文件',param)
          this.newFile=res.data          
          this.$toast.clear()
          this.addSearon()
        })
    },
    //新增季度检查
    addSearon(){
      if(!this.newFile) return
      let userData = this.$store.getters.userData
      let fileName = userData.companyAbbreviation+'-'+userData.companyBusinessLicense+'-'+ formatDate(new Date(),'yyyyMMdd')
      let params = {
        fileName:fileName+this.fileTypeName,
        filePath:this.newFile.url,
        projectId:this.$route.query.projectId,
        fileType:0,
        lendingId:this.$route.query.id,
        timeQuarter:this.isQuarter,
      }
      addSearonCheck(params).then(res=>{
        console.log('新增季度检查',res)
        this.$notifySty('添加成功')
        this.changeIdx(this.isQuarter,'get')
      })
    },
    //季度选择改变
    changeIdx(idx,type){
      console.log('//季度选择改变',idx)
      this.newFile={}
      this.isQuarter = idx
      this.pageLog = 1
      this.size = 5
      this.totalLog = null
      this.$set(this,'LoanedList',[])
      this.loadingLog = true// 加载状态结束
      this.finished_text = ''
      this.getNowUpType(type)
      this.onLoad()
    },
    boxShow(itm){
      this.show = !this.show
      if(!this.show)this.delItm = null
      else this.delItm = itm
      console.log('this.delItm',this.delItm)
    },
    //'删除文件'
    deleteFile(){
      // console.log('this.delItm',this.delItm)
      updateRentBackFileById({id:this.delItm.id}).then(res=>{
        // console.log('删除文件',res)
        this.LoanedList.map((d,idx)=>{
          if(d.id==this.delItm.id){ 
            this.LoanedList.splice(idx,1)
          }
          this.boxShow()
        })
      })
    },
    //下载
    download(itm){
      console.log('itm',itm)
      let baseUrl = this.$baseUrl()+itm.filePath+'&filename='+itm.fileName
      window.open(baseUrl)
    },
    // 加载数据
    onLoad() {
      const that = this
      // console.log(this.LoanedList.length , this.totalLog)
      if (this.LoanedList.length === this.totalLog) {
        this.finishedLog = true // 数据全部加载完成
        this.loadingLog = false// 加载状态结束
        this.finished_text = '没有更多数据'
        return
      }
      // 下拉加载事件
      setTimeout(() => {
        that.getLoanedList()
      }, 1000)      
    },
    //获取当前数据列表
    getLoanedList() {
      // console.log('季度检查list111', this.totalLog)
      const params = {
        projectId:this.$route.query.projectId,
        fileType: 0,
        page: this.pageLog,
        size: this.size,
        timeQuarter:this.isQuarter,
        createTime:this.newYear
      }
      rentBackFilePage(params).then((res) => {
        // console.log('季度检查list222',this.totalLog)
        this.loadingLog = false// 加载状态结束
        this.totalLog = res.total
        if(res.total==0){
          this.finished_text = '没有更多数据'
          return
        }
        this.LoanedList = this.LoanedList.concat(res.data)
        this.pageLog += 1
      })
    },   
    //贷后提醒发送
    callLoad(){
      let params = {
        lendingInfoId:this.$route.query.id
      }
      sendTextCardMessage(params).then(res=>{
        this.$notifySty('提醒已发送')
        console.log(res)
        
        this.sendMessage = res.data
        this.sendMessage.paramInfo = JSON.parse(this.sendMessage.paramInfo)
        this.sendMessage.sendInfo = JSON.parse(this.sendMessage.sendInfo)
        this.sendMessage.date = formatDate(new Date(),'yyyy-MM-dd hh:mm:ss') 

        console.log(this.sendMessage)
        this.showMoreBox = true
        // this.sendMessage.wechatInfo = JSON.parse(this.sendMessage.wechatInfo)
      })
    }, 
    //查询当前季度上传文件状态
    getNowUpType(type){
      let params = {
        lendingInfoId:this.$route.query.id,
        year:this.newYear,
        timeQuarter:this.isQuarter
      }
      if(type=='get'){
        getLendingInfoConfirm(params).then(res=>{
          this.nowUpType = res.data
        })
      }else{
        lendingInfoConfirm(params).then(res=>{
          this.nowUpType = res.data
        })
      }
      
    },

    //年份变化
    changeYear(){
      this.changeIdx(this.isQuarter,'get')
    },
    showMore() {
      this.showMoreBox = !this.showMoreBox
    },
  },
}
</script>
